import React, { useState } from "react";
import { useRef } from "react";
import { useCallback } from "react";
import { useEffect } from "react";
import ReactPlayer from "react-player";
import { getPlaylist } from "../api";
import { useSocket } from "../hooks/useSocket";
import { Song, SongItem } from "./Song";
import { Spinner } from "./Spinner";
import { VideoWrapper } from "./VideoWrapper";

export const Playlist = () => {
  const {
    socketConnected,
    socketId,
    socketRef,
    emitterRef,
    isSyncMode,
    isHost,
  } = useSocket();
  const [playlist, setPlayList] = useState<Song[]>([]);
  const [playing, setPlaying] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [subscribed, setSubscribed] = useState<boolean>(false);
  const playerRef = useRef<ReactPlayer>(null);
  const [currentSong, setCurrentSong] = useState<
    Song & { index: number; duration?: number; playedSeconds?: number }
  >({
    _id: {
      $oid: "",
    },
    user: "",
    url: "",
    thumbnail_url: "",
    status: "pending",
    title: "",
    description: "",
    index: 0,
  });
  const appendToPlaylist = useCallback(
    (newItem: Song) => {
      setPlayList([...playlist, newItem]);
    },
    [playlist]
  );
  const canControl = isSyncMode ? (isHost ? true : false) : true;
  const handleSeek = useCallback(
    (data: string) => {
      const {
        video_id,
        seek,
        video_index,
      }: { video_id: string; seek: string; video_index: string } =
        JSON.parse(data);
      const currentSongId = currentSong._id.$oid;
      const seekNum = parseFloat(seek);
      const vidIndex = parseInt(video_index, 10);
      if (currentSongId === video_id) {
        setCurrentSong({ ...currentSong, playedSeconds: seekNum });
      }
      if (currentSongId !== video_id && playlist.length > vidIndex) {
        // todo handle for video not found
        const song = playlist[vidIndex];
        setCurrentSong({ ...song, playedSeconds: seekNum, index: vidIndex });
      }
    },
    [currentSong, playlist]
  );
  const handlePlayPause = useCallback(
    (data: string) => {
      const {
        video_id,
        video_index,
        seek,
        playing,
      }: {
        video_id: string;
        seek: string;
        playing: string;
        video_index: string;
      } = JSON.parse(data);
      const currentSongId = currentSong._id.$oid;
      const seekNum = parseFloat(seek);
      const vidIndex = parseInt(video_index, 10);
      if (currentSongId === video_id) {
        setCurrentSong({ ...currentSong, playedSeconds: seekNum });
      }
      if (currentSongId !== video_id && playlist.length > vidIndex) {
        // todo handle for video not found
        const song = playlist[vidIndex];
        setCurrentSong({ ...song, playedSeconds: seekNum, index: vidIndex });
      }
      setPlaying(playing === "true");
    },
    [currentSong, playlist]
  );
  const getSocket = useCallback(() => socketRef?.current, [socketRef]);

  const sendPlayPauseMessage = useCallback(
    (isPlaying: boolean) => {
      if (isHost && isSyncMode) {
        getSocket()?.send(
          `/pp ${currentSong._id.$oid} ${
            currentSong.playedSeconds || 0
          } ${isPlaying} ${currentSong.index || 0}`
        );
      }
    },
    [currentSong, isSyncMode, isHost, getSocket]
  );

  const sendSeekMessage = useCallback(
    (seconds: number) => {
      if (isHost && isSyncMode) {
        getSocket()?.send(
          `/seek ${currentSong._id.$oid} ${seconds || 0} ${
            currentSong.index || 0
          }`
        );
      }
    },
    [isHost, isSyncMode, currentSong, getSocket]
  );
  const syncMusic = useCallback(() => {
    sendPlayPauseMessage(playing);
  }, [sendPlayPauseMessage, playing]);

  useEffect(() => {
    const socket = socketRef?.current;
    const emitter = emitterRef?.current;
    if (
      emitter &&
      socketId &&
      socketConnected &&
      socket &&
      isHost &&
      isSyncMode
    ) {
      emitter.on("syncRoomJoined", syncMusic);
    }
    return () => {
      emitter?.removeListener("syncRoomJoined", syncMusic);
    };
  }, [
    socketRef,
    emitterRef,
    socketConnected,
    socketId,
    syncMusic,
    playing,
    isHost,
    isSyncMode,
  ]);
  useEffect(() => {
    const socket = socketRef?.current;
    const emitter = emitterRef?.current;
    if (!subscribed) {
      if (isSyncMode) {
        if (socket && socketConnected && socketId && emitter) {
          setSubscribed(true);
          console.log("added sync mode listeners");
          emitter.on("playPause", handlePlayPause);
          emitter.on("seek", handleSeek);
        }
      }
    }
  }, [
    isSyncMode,
    socketRef,
    emitterRef,
    socketId,
    socketConnected,
    playlist,
    subscribed,
    handlePlayPause,
    handleSeek,
  ]);

  useEffect(() => {
    const emitter = emitterRef?.current;
    return () => {
      console.log("removed sync mode listeners");
      emitter?.removeListener("playPause", handlePlayPause);
      emitter?.removeListener("seek", handleSeek);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    const socket = socketRef?.current;
    const emitter = emitterRef?.current;
    const onNewSong = (data: string) => {
      const json: Song = JSON.parse(data);
      appendToPlaylist(json);
    };
    if (socketConnected && socket && socketId && emitter) {
      console.log("all done");
      emitter.on("newSong", onNewSong);
    }
    return () => {
      console.log("Clear");
      emitter?.removeListener("newSong", onNewSong);
    };
  }, [socketId, socketConnected, socketRef, emitterRef, appendToPlaylist]);

  useEffect(() => {
    const fetchPlaylist = async () => {
      try {
        setLoading(true);
        const playlist = await getPlaylist();
        setPlayList(playlist);
      } catch (err) {
        console.log(err);
      } finally {
        setLoading(false);
      }
    };
    fetchPlaylist();
  }, []);

  useEffect(() => {
    if (playlist.length && !playing && !currentSong.url) {
      setCurrentSong({ ...currentSong, ...playlist[0], index: 0 });
    }
  }, [playlist, playing, currentSong]);

  const { playedSeconds, duration } = currentSong;
  const progress =
    playedSeconds && duration
      ? Math.round((playedSeconds / duration) * 100)
      : 0;

  if (loading) {
    return (
      <div className="max-height bg-dark d-flex align-items-center justify-content-center w-full">
        <Spinner />
      </div>
    );
  }
  return (
    <div className="overflow-hidden d-flex bg-dark">
      <div className="flex-grow-1 col max-height">
        {!canControl && (
          <div
            style={{
              width: "100%",
              height: "100%",
              zIndex: 1,
              position: "absolute",
            }}
          />
        )}
        <ReactPlayer
          ref={playerRef}
          url={currentSong.url}
          playing={playing}
          onStart={() => {
            sendPlayPauseMessage(true);
            setPlaying(true);
          }}
          onPlay={() => {
            sendPlayPauseMessage(true);
            setPlaying(true);
          }}
          onPause={() => {
            sendPlayPauseMessage(false);
            setPlaying(false);
          }}
          pip
          onSeek={(seconds) => {
            if (canControl) {
              sendSeekMessage(seconds);
            }
          }}
          onProgress={({ playedSeconds }) => {
            setCurrentSong({
              ...currentSong,
              playedSeconds: playedSeconds,
            });
          }}
          onDuration={(duration) => {
            console.log({ duration });
            setCurrentSong({
              ...currentSong,
              duration,
            });
          }}
          onEnded={() => {
            if (playlist.length) {
              const nextIndex = currentSong.index + 1;
              if (nextIndex === playlist.length) {
                setCurrentSong({ ...playlist[0], index: 0 });
              } else {
                setCurrentSong({ ...playlist[nextIndex], index: nextIndex });
              }
            }
          }}
          width={"100%"}
          wrapper={VideoWrapper}
        />
      </div>
      <div className="flex-shrink-0 col-sm-4 overflow-auto max-height">
        <ul className="list-group list-group-flush">
          {playlist.map((song, index) => {
            const isCurrent = currentSong._id?.$oid === song._id?.$oid;
            const nextSong = currentSong.index + 1;
            const nextSongIndex = nextSong === playlist.length ? 0 : nextSong;
            return (
              <SongItem
                className={`${isCurrent ? "bg-black" : "bg-dark"}`}
                key={song._id?.$oid}
                onClick={(e) => {
                  e.preventDefault();
                  if (!isCurrent && canControl) {
                    setCurrentSong({ ...song, index });
                    setPlaying(true);
                  }
                }}
                song={song}
                isCurrent={isCurrent}
                playing={playing}
                isNextSong={nextSongIndex === index}
                duration={duration || 0}
                progress={progress}
              />
            );
          })}
        </ul>
      </div>
    </div>
  );
};
