import { useEffect } from "react";
import { useSocket } from "../hooks/useSocket";
import { Switch } from "./Switch";

export const Navbar = () => {
  const {
    socketConnected,
    socketId,
    socketRef,
    isSyncMode,
    setIsSyncMode,
    isHost,
  } = useSocket();
  const syncMode = ({
    target: { checked },
  }: React.ChangeEvent<HTMLInputElement>) => {
    setIsSyncMode(checked);
  };

  useEffect(() => {
    const socket = socketRef?.current;
    if (socketConnected && socketId && socket) {
      if (isSyncMode) {
        socket.send("/join sync");
      } else {
        socket.send("/leave sync");
      }
    }
  }, [isSyncMode, socketRef, socketConnected, socketId]);
  return (
    <nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark">
      <div className="container-fluid">
        <a className="navbar-brand" href="/">
          Innovate Playlist
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarText"
          aria-controls="navbarText"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarText">
          <ul className="navbar-nav ms-auto">
            <li className="nav-item">
              {isSyncMode ? (
                isHost ? (
                  <span
                    className="nav-link badge bg-success text-dark"
                    aria-current="page"
                  >
                    Host
                  </span>
                ) : (
                  <span
                    className="nav-link badge bg-light text-dark"
                    aria-current="page"
                  >
                    Guest
                  </span>
                )
              ) : undefined}
            </li>
          </ul>
          <form className="d-flex ms-auto">
            <Switch onChange={syncMode} checked={isSyncMode}>
              Sangai Sunam (Beta)
            </Switch>
          </form>
        </div>
      </div>
    </nav>
  );
};
